<template>
  <div class="container">
    <div class="form">
      <version-query-form
      :personForm="personForm"
      :endMonthShow="true"
      :timeRangeShow="false"
      :versionShow="true"
      ref="versionQueryForm">
      </version-query-form>
    </div>
    <div class="option">
      <el-button type="primary" size="small" plain @click="inspectTable">检视</el-button>
      <el-button  type="primary" size="small" plain @click="exportTable">导出报表</el-button>
    </div>
    <div class="data">
      <div class="table">
        <base-table
          :columns="columns"
          :data="mergeData"
          row-key="id"
          :spanMethod="merge"
          :mountedCallBack="tick">
