          premiumScale: '2564.55',
          expIncomePercentBiz: '14.97',
          realIncomePercentBiz: '16.77',
          disIncomePercentBiz: '1.8',
          expIncomePercentForce: '28.00',
          realIncomePercentForce: '29.90',
          disIncomePercentForce: '1.90',
          expComAuto: '90.00',
          realComAuto: '96.30',
