          disFinal: '-0.4',
          realFull: '59.60'
        },
        {
          id: '6',
          bizSector: '家用-新车',
          bizSmallNumber: '01020302',
          bizSmallDetail: '单交,交商_交三_有附加',
          premiumScale: '2514.45',
