          realIncomePercentBiz: '16.43',
          disIncomePercentBiz: '-2.3',
          expIncomePercentForce: '30.00',
          realIncomePercentForce: '30.20',
          disIncomePercentForce: '0.20',
          expComAuto: '85.00',
          realComAuto: '85.20',
          disComAuto: '0.20',
          expOperation: '97.4',
          realOperation: '97.3',
          disOperation: '-0.1',
          expMarket: '17.2',
          realMarket: '18.5',
          disMarket: '1.3',
          expFinal: '57.2',
          realFinal: '57.6',
          disFinal: '0.4',
          realFull: '58.60'
        },
        {
          id: '7',
          bizSector: '合计',
          bizSmallNumber: '',
          bizSmallDetail: '',
          premiumScale: '15299.9',
          expIncomePercentBiz: '17.01',
          realIncomePercentBiz: '16.67',
          disIncomePercentBiz: '-0.34',
          expIncomePercentForce: '28.51',
          realIncomePercentForce: '28.72',
          disIncomePercentForce: '0.21',
          expComAuto: '62.49',
          realComAuto: '64.08',
          disComAuto: '1.59',
          expOperation: '97.63',
          realOperation: '97.74',
          disOperation: '0.104',
          expMarket: '18.03',
          realMarket: '19.09',
          disMarket: '1.06',
