          realFull: '58.79'
        }
      ]
    }
  },
  computed: {
    mergeData () {
      const props = ['bizSector']
      return tableDataMerge(this.singleVersionData, props)
    }
  },
  methods: {
    merge ({row, column, rowIndex, columnIndex}) {
      const props = ['bizSector']
      if (props.includes(column.property)) {
        if (row.rowSpan[column.property] > 1) {
          return [row.rowSpan[column.property], 1]
        } else if (row.rowSpan[column.property] === 0) {
          return [0, 0]
        }
      }
    },
    tick () {
      const footer = document.getElementsByClassName('el-table__body')[0]
      const tbody = footer.getElementsByTagName('tbody')[0]
      const tr = tbody.getElementsByTagName('tr')[6]
      const tds = tr.getElementsByTagName('td')
      tr.removeChild(tds[1])
      tr.removeChild(tds[1])
      tds[0].setAttribute('colspan', '3')
    },
    inspectTable () {
    },
    exportTable () {
    },
    sum ({ columns, data }) {
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计'
        } else if (index === 1 || index === 2) {
          sums[index] = ''
        } else {
          sums[index] = 0
          data.forEach(row => {
            sums[index] += Number(row[column.property])
          })
          if (index >= 4) {
            sums[index] /= data.length
          }
          sums[index] = sums[index].toFixed(2)
        }
      })
      return sums
    }
  }
}
</script>

<style scoped>

</style>



